.App {
  text-align: center;
}

.container.PhaserJS {
  text-align: left;
}

.datalist dt {
  margin-top: 20px;
}

.datalist dd {
  margin-top: 12px;
}

.heading {
  margin-top: 40px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Profile {
  margin-left: auto;
  margin-right: auto;
}

h1 {
  font-size: 64px;
}

h2 {
  margin-top: 30px;
  margin-bottom: 30px;
}

.table.table-bordered {
  font-weight: bold;
}

table.table.table-bordered tbody {
  font-size: 13px;
}

table.table.table-bordered thead {
  font-size: 19px;
}